import React from 'react';

const TextCapture = ({ submitClick, submitField })=>{

	return (
		<div>
			<input type="text" onChange={submitField} />
			<button id="submitButton" onClick={submitClick}> Submit! </button>
		</div>
	);

}



export default TextCapture;



